.add-todo-container {
  position: sticky;
  top: 12px;
  padding: 0 10px 0 10px;
  margin-top: 124px;
  width: 100%;
  max-width: 540px;
  background: #fff;
  box-shadow: 0 12px 12px 6px #fff, 0 -24px 12px 0 #fff;
  z-index: 100;

  > form.add-todo {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    height: 48px;

    > input {
      flex: 1;
      padding: 10px 16px;
      border: 3px solid #e0e0e0;
      font-size: 16px;
      width: 100%;
      height: 100%;

      &:focus {
        border-color: #9ed4e8;
        outline: none;
      }
      &::placeholder {
        color: #bbbbbb;
      }

      border-radius: 24px 9px 9px 24px;
      -webkit-border-radius: 24px 9px 9px 24px;
      -moz-border-radius: 24px 9px 9px 24px;
      -ms-border-radius: 24px 9px 9px 24px;
      -o-border-radius: 24px 9px 9px 24px;
    }

    > button {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-left: 3px;
      padding: 0;
      padding-right: 3px;
      border: 3px solid #e0e0e0;
      background-color: #f0f0f0;
      font-size: 30px;
      color: #999999;
      height: 100%;
      width: 48px;
      cursor: pointer;

      &.active {
        border: 3px solid #e0e0e0;
        background-color: #e0e0e0;
        color: #555555;

        &:hover {
          border: 3px solid #d2d2d2;
          background-color: #d2d2d2;
        }
      }

      > span {
        font-size: 24px;
        font-weight: 700;
      }

      border-radius: 9px 24px 24px 9px;
      -webkit-border-radius: 9px 24px 24px 9px;
      -moz-border-radius: 9px 24px 24px 9px;
      -ms-border-radius: 9px 24px 24px 9px;
      -o-border-radius: 9px 24px 24px 9px;
    }
  }

  > p.add-todo-hint {
    margin: 6px 0 0 18px;
    font-size: 12px;
    color: #999;
    height: 16px;

    &.hidden {
      margin-top: -6px;
      opacity: 0;
    }
  }
}
